@import url("https://fonts.googleapis.com/css2?family=Cairo:wght@300;400;500;600;700&display=swap");
*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

::-webkit-scrollbar {
  width: 10px;
  border-radius: 15px;
}

::-webkit-scrollbar-track {
  background-color: transparent;
}

::-webkit-scrollbar-thumb {
  background-color: #888;
  border-radius: 15px;
}

body {
  font-family: "Cairo", sans-serif;
  overflow-x: hidden;
  background-color: #f8fffc;
}

.grid {
  display: -ms-grid;
  display: grid;
}

.flex {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}

.btn {
  padding: 7px 15px;
  border-radius: 3px;
  outline: none;
  border: none;
  background-color: unset;
  cursor: pointer;
  text-decoration: none;
  color: #000;
  display: inline-block;
  font-family: inherit;
}

.text-primary {
  color: #009153;
}

.text-secondary {
  color: #ffc01e;
}

.btn-primary {
  background-color: #009153;
  color: #fff;
  font-weight: 600;
}

.btn-secondary {
  background-color: #ffc01e;
  color: #000;
  font-weight: 600;
}

.center {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

.emoji {
  font-weight: initial;
}
